import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const router = new VueRouter({
  mode: 'history',
  routes: [
    {
      path: '/main',
      name: 'Main',
      meta: {layout: 'main'},
      component: () => import('../views/Main.vue')
    },
    {
      path: '/login',
      name: 'Login',
      meta: {layout: 'login'},
      component: () => import('../views/Login.vue')
    },
    {
      path: '/reg',
      name: 'Reg',
      meta: {layout: 'reg'},
      component: () => import('../views/Reg.vue')
    }
  ],
  base: process.env.BASE_URL,
})

export default router
